.navbar-fontlogo {
    font-size: 1.1rem;
    transition: all 200ms ease-in-out;
}

.navbar {
    display: flex;
    justify-content: center;
    align-items: center;

    max-height: 80px;
    background-color: var(--color-bg);
    transition: box-shadow 1s linear;
}
.shadow {
    box-shadow: 0 0 0.5rem rgb(0,0,0,0.5);
}

.navbar-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    width: 80vw;
    max-width: 1080px;
    transition: height 0.2s linear;
}
.height-60 {
    height: 60px;
}
.height-80 {
    height: 80px;
}

.navbar-links_logo {
    display: flex;
    justify-content: flex-start;

    height: 100%;
}
.navbar-links_logo img {
    height: 100%;
}

.navbar-links_container {
    display: flex;
    align-items: center;

    height: 100%;
}

.navbar-links_container ul{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2rem;

    list-style: none;
    height: 100%;
}
.navbar-links_container ul li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.2rem;
}
.navbar-links_container ul li:hover .navbar-links_container_dropdown {
    clip-path: inset(0 0 0 0);
}
.navbar-links_container ul li:hover .navbar-fontlogo {
    transform: rotate(-450deg);
}
.navbar-links_container a {
    display: flex;
    align-items: center;

    font-family: var(--font-family);
    color: var(--color-text);
}

.navbar-links_container_dropdown {
    position: absolute;
    z-index: 1;
    top: 100px;
    padding: 1rem;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    gap: 1rem;

    width: 14rem;
    background-color: var(--color-bg);
    border-top: 3px solid var(--color-border);
    clip-path: inset(0 0 100% 0);
    transition: all 200ms ease-in-out;
}

.navbar-links_container_dropdown a {
    width: 100%;
    padding: 0.3rem 0.5rem;
    border: 1px solid var(--color-sub-border);
}
.navbar-links_container_dropdown a:hover {
    background-color: rgba(0,0,0,0.03);
}
